@import url("https://fonts.googleapis.com/css?family=Comfortaa|Kanit:900");
body {
  font-family: 'Comfortaa', cursive; }

#logo {
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat; }

header h1 {
  font-weight: bold; }

section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap; }
  section div {
    white-space: nowrap; }

@media screen {
  html {
    font-size: 2vw; }
  body {
    color: #594f51;
    margin: 0;
    margin: 0 1rem;
    padding: 0;
    display: flex;
    flex-direction: column; }
  ul {
    line-height: 2rem;
    padding-left: 1rem;
    list-style: none;
    margin-top: 0; }
    ul li:before {
      content: '-';
      color: #ed114e;
      margin-right: 0.5rem; }
  p {
    margin-top: 0;
    margin-bottom: 0;
    line-height: 2rem; }
  main {
    max-width: 1200px;
    margin: auto; }
  a {
    text-decoration: none;
    color: #ed114e; }
    a:hover {
      text-decoration: underline; }
  h1, h2 {
    padding: 0;
    margin: 0; }
  h2 {
    color: #ed114e;
    font-weight: normal;
    font-family: 'Kanit', cursive; }
  b {
    color: #231f20; }
  .fa {
    color: #ed114e; }
  article {
    margin-bottom: 4rem;
    padding-bottom: 2rem; }
    article:last-child {
      border: 0;
      margin: 0;
      padding: 0;
      min-height: 100vh; }
      article:last-child div {
        line-height: 2rem; }
    article h2 {
      text-transform: uppercase;
      border-bottom: 1px solid #231f20;
      margin-bottom: 0.5rem; }
  footer {
    background: #231f20;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 1rem; }
  header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 2rem; } }

